.google_auth{
   margin: 0 auto;
   margin-top: 150px;
   border: 1px solid lightgrey;
   padding: 30px;
   width: 35vw;
   word-wrap: break-word;
   border-radius: 20px;
   box-shadow: 0px 0px 10px grey;
}
.google_auth_container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo__section{
    display: flex;
    align-items: center;
    font-size: 27px;
    font-weight: 600;
}
.logo__section > img{
    width: 70px;
    object-fit: contain;
    margin-right: 5px;
}
.google__body{
    display: flex;
    flex-direction: column;
    margin-top: 30px;
}
.google__body > p{
    font-size: 20px;
}
.google__body span{
    font-size: 15px;
    color: grey;
    margin-top: 20px;
}
.google_auth Button{
    background-color: blue !important;
    color: white !important;
    text-transform: inherit !important;
    border-radius: 5px;
    margin-top: 30px;
}